<template>
    <div>

        
        <v-row>
            
            <v-col cols="10" align="center">

                <v-data-table v-model="selected"
                              show-select
                              :headers="headers"
                              :items="mascotas"
                              :search="search"
                              :options.sync="pagination"
                              class="elevation-1">
                <template v-slot:items="props">
                        <tr :active="props.selected" @click="props.selected = !props.selected">
                            <td class="text-xs-right">{{ props.item.nombre }}</td>
                            <td class="text-xs-right">{{ props.item.nacimiento }}</td>
                            <td class="text-xs-right">{{ props.item.sexo }}</td>
                            <td class="text-xs-right">{{ props.item.especie }}</td>
                            <td class="text-xs-right">{{ props.item.raza }}</td>
                            <td class="text-xs-right">{{ props.item.peso }}</td>
                        </tr>
                </template>
                </v-data-table>

            </v-col>
            <v-col cols="2" align="right">
                <v-icon size="55" @click="ayuda"> mdi-help-circle-outline</v-icon>
            </v-col>
            

        </v-row>
        <v-row>
            
            <v-col cols="12" align="center">
                <router-link style="text-decoration: none; color: inherit;" to="/perfil">

                    <v-btn color="#386653" class="white--text" @click="eliminar">
                        Eliminar Mascota
                    </v-btn>
                </router-link>
            </v-col>
        </v-row>

    </div>

</template>

<script>
    export default {
        data() {
            return {
                search: '',
                pagination: {},
                selected: [],
                headers: [
                    {
                        text: 'Nombre',
                        align: 'left',
                        sortable: false,
                        value: 'nombre'
                    },
                    {
                        text: 'Fecha Nacimiento',
                        align: 'center',
                        value: 'nacimiento'
                    },
                    {
                        text: 'Sexo',
                        align: 'left',
                        sortable: false,
                        value: 'sexo'
                    },
                    {
                        text: 'Especie',
                        align: 'left',
                        sortable: false,
                        value: 'especie'
                    },
                    {
                        text: 'Raza',
                        align: 'left',
                        sortable: false,
                        value: 'raza'
                    },
                    {
                        text: 'Peso',
                        align: 'left',
                        sortable: false,
                        value: 'peso'
                    },
                ],
                mascotas: [
                    {
                        nombre: 'Kira',
                        nacimiento: '01-12-2008',
                        sexo: 'Hembra',
                        especie: 'Canino',
                        raza: 'Cocker',
                        peso: '14 kg',
                    },
                    {
                        nombre: 'Enzo',
                        nacimiento: '05-07-2020',
                        sexo: 'Macho',
                        especie: 'Canino',
                        raza: 'Mini Pincher',
                        peso: '3 kg',
                    },
                    {
                        nombre: 'Tina',
                        nacimiento: '15-09-2008',
                        sexo: 'Hembra',
                        especie: 'Canino',
                        raza: 'Teckel',
                        peso: '13 kg',
                    },
                ]

            }
        },
        toggleAll() {
            if (this.selected.length) this.selected = []
            else this.selected = this.mascotas.slice()
        },
        methods: {
            toggleOrder() {
                this.pagination.descending = !this.pagination.descending
            },
            nextSort() {
                let index = this.headers.findIndex(h => h.value === this.pagination.sortBy)
                index = (index + 1) % this.headers.length
                index = index === 0 ? index + 1 : index
                this.pagination.sortBy = this.headers[index].value
            },
            eliminar() {
                alert('La mascota ha sido eliminada');
            },
            ayuda() {
                alert('Selecciona la mascota que quieras eliminar de tu perfil y pulsa eliminar. En caso de no querer eliminar ninguna pulsa el botón "Atrás".')
            }
            
            
            
        }
    }
</script>

