<template>

        <v-row>
            <v-col cols="2">
                <v-spacer> </v-spacer>
            </v-col>
            <v-col cols="8" >
                <v-data-table
                    :headers="headers"
                    :items="mascotas"
                    sort-by="date"
                    class="elevation-1"
                >
                    <template v-slot:top>
                        <v-toolbar
                            flat
                        >
                            <v-toolbar-title>CITAS</v-toolbar-title>        
                            <v-divider
                            class="mx-4"
                            inset
                            vertical
                            ></v-divider>
                            <v-spacer></v-spacer>
                            <v-dialog
                            v-model="dialog"
                            max-width="500px"
                            >
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                color="#62C251"
                                dark
                                class="black--text"
                                v-bind="attrs"
                                v-on="on"
                                >
                                Nueva Cita
                                </v-btn>
                            </template>
                            <v-card>
                                <v-card-title>
                                <span class="headline">{{ formTitle }}</span>
                                </v-card-title>

                                <v-card-text>
                                <v-container>
                                    <v-row>
                                    <v-col
                                        cols="12"
                                        sm="6"
                                        md="4"
                                    >
                                       <v-select
                                          v-model="editedItem.name"
                                          :items="items"
                                          item-text="state"
                                          label="Nombre"
                                          single-line
                                        ></v-select>
                                    </v-col>




                                    <v-col
                                        cols="12"
                                        sm="6"
                                        md="4"
                                    >
                                        <v-menu
                                      ref="menu3"
                                      v-model="menu3"
                                      :close-on-content-click="false"
                                      :nudge-right="40"
                                      :return-value.sync="time"
                                      transition="scale-transition"
                                      offset-y
                                      max-width="290px"
                                      min-width="290px"
                                    >
                                      <template v-slot:activator="{ on, attrs }">
                                        <v-text-field
                                          v-model="editedItem.date"
                                          label="Fecha"
                                          v-bind="attrs"
                                          v-on="on"
                                        ></v-text-field>
                                      </template>
                                      <v-date-picker
                                        v-if="menu3"
                                        name="calendario"
                                        v-model="editedItem.date"
                                        full-width
                                        @click:minute="$refs.menu3.save(date)"
                                      ></v-date-picker>
                                     </v-menu>

                                    </v-col>




                                    
                                    <v-col
                                        cols="12"
                                        sm="6"
                                        md="4"
                                    >
                                    <v-menu
                                      ref="menu"
                                      v-model="menu2"
                                      :close-on-content-click="false"
                                      :nudge-right="40"
                                      :return-value.sync="time"
                                      transition="scale-transition"
                                      offset-y
                                      max-width="290px"
                                      min-width="290px"
                                    >
                                      <template v-slot:activator="{ on, attrs }">
                                        <v-text-field
                                          v-model="editedItem.time"
                                          label="Hora"
                                          prepend-icon="mdi-clock-time-four-outline"
                                          v-bind="attrs"
                                          v-on="on"
                                        ></v-text-field>
                                      </template>
                                      <v-time-picker
                                        v-if="menu2"
                                        v-model="editedItem.time"
                                        full-width
                                        @click:minute="$refs.menu.save(time)"
                                      ></v-time-picker>
                                    </v-menu>
                                    </v-col>
                                    </v-row>
                                </v-container>
                                </v-card-text>

                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="close"
                                >
                                    Cancelar
                                </v-btn>
                                <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="save"
                                >
                                    Guardar
                                </v-btn>
                                </v-card-actions>
                            </v-card>
                            </v-dialog>
                            <v-dialog v-model="dialogDelete" max-width="500px">
                            <v-card>
                                <v-card-title class="headline">¿ Está seguro de que desea borrar la cita seleccionada ?</v-card-title>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                                <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>
                            </v-dialog>
                        </v-toolbar>
                    </template>
                    <template v-slot:item.actions="{ item }">
                    <v-icon
                        small
                        class="mr-2"
                        @click="editItem(item)"
                    >
                        mdi-pencil
                    </v-icon>
                    <v-icon
                        small
                        @click="deleteItem(item)"
                    >
                        mdi-delete
                    </v-icon>
                    </template>
                    <template v-slot:no-data>
                    <v-btn
                        color="primary"
                        @click="initialize"
                    >
                        Restaurar
                    </v-btn>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
    
</template>
<script>
  export default {
    data: () => ({
      time: null,
      menu2: false,
      menu3: false,
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Nombre Mascota',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Date', value: 'date' },
        { text: 'Hora', value: 'time' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      mascotas: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        date: '',
        time: ''
      },
      defaultItem: {
        name: '',
        date: '',
        time: ''
      },
       items: [
          { state: 'Kira'},
          { state: 'Enzo'},
          { state: 'Tina'},

        ],
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.mascotas = [
         {
            name: 'Kira',
            date: '2020-12-15',
            time: '16:45'
        },
        {
            name: 'Zeus',
            date: '2020-12-15',
            time: '17:45'
        },
        {
            name: 'Flufly',
            date: '2020-12-16',
            time: '18:00'
        },
        {
            name: 'Kira',
            date: '2020-12-15',
            time: '16:45'
        },
        {
            name: 'Zeus',
            date: '2020-12-15',
            time: '17:45'
        },
        {
            name: 'Flufly',
            date: '2020-12-15',
            time: '18:00'
        },
            
        ]
      },

      editItem (item) {
        this.editedIndex = this.mascotas.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.mascotas.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.mascotas.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.mascotas[this.editedIndex], this.editedItem)
        } else {
            if (this.editedItem.name == '' || this.editedItem.time == '' || this.editedItem.date == '') alert('Debes rellenar todos los campos para solicitar la cita.')
            else {
                var hoy = new Date();
                var horas = this.editedItem.time.split(':');
                if (Date.parse(this.editedItem.date) < hoy) alert('¡Escoge una fecha de hoy en adelante!')
                else if (horas[0] < '10' || horas[0] > '20') alert('El horario de la clínica es de 10:00 a 20:00.')
                else {
                    this.mascotas.push(this.editedItem)
                    this.editedItem.name == ''
                    this.editedItem.date == ''
                    this.editedItem.time == ''
                    this.close()
                }
           
            }
        }
        
      },
    },
  }

</script>

 