<template>
      <div id="general" >
      
      <div id = "registro">
      <v-form
          ref="form"
          v-model="valid"
          lazy-validation
      >
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
         <b><v-text-field
          v-model="username"
          :rules="usernameRules"
          label="Nombre de usuario: "
          required
          ></v-text-field></b>
          
          <b><v-text-field
          v-model="email"
          :rules="emailRules"
          label="Correo electrónico: "
          required
          ></v-text-field></b>

         <b> <v-text-field
          v-model="name"
          :counter="10"
          :rules="nameRules"
          label="Nombre: "
          required
          ></v-text-field></b>

         <b> <v-text-field
          v-model="apellidos"
          :counter="30"
          :rules="apellidosRules"
          label="Apellidos: "
          required
          ></v-text-field></b>
        
          
          <b><v-text-field
          v-model="direccion"
          :rules="direccionRules"
          :counter="50"
          label="Dirección completa: "
          required
          ></v-text-field></b>

         <b> <v-text-field
          v-model="password"
          :counter="8"
          :rules="passwordRules"
          label="Contraseña: "
          required
          ></v-text-field></b>
          
          <v-checkbox
          v-model="checkbox"
          :rules="[v => !!v || 'Debes aceptar los términos de privacidad para continuar']"
          label="Por favor, acepta los términos de privacidad"
          required
          ></v-checkbox>
          <v-row >
            <v-col cols="5" align="center">
          <v-btn v-if=" !checkbox"
          :disabled="!valid"
         color="#62C251" 
          @click="validate"
          >
          Validar
          </v-btn>
          
          <router-link style="text-decoration: none; color: inherit;" v-if=" checkbox" to="/">
          <v-btn 
          :disabled="!valid"
          color="#62C251"
          @click="validate"
          >
          Validar
          </v-btn>
          </router-link>
          </v-col>

          <v-col cols="5" align="center">
          <v-btn
          color="#62C251"
          @click="reset"
          >
          Restaurar el formulario
          </v-btn>
          </v-col>
          </v-row>

      </v-form>
      </div>

      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-spacer></v-spacer>
        </v-col>
      </v-row>
    </div>
</template>
<script>
  
  export default {
    data: () => ({
      
      valid: false,
      name: '',
      usuarios: [],
      nameRules: [
        v => !!v || 'El nombre es obligatorio',
        v => (v && v.length <= 10) || 'El nombre tiene que tener menos de 10 caracteres'
      ],
      apellidos:'',
      apellidosRules:[
        
        v => !!v || 'Añade al menos un apellido',
        v => (v && v.length <= 30) || 'Los apellidos tienen que tener menos de 30 caracteres'
      ],
      direccion:'',
      direccionRules:[
        
        v => !!v || 'Añade la dirección',
        v => (v && v.length <= 50) || 'La dirección tienen que tener menos de 50 caracteres'
      ],
      username:'',
      usernameRules:[
        v => !!v || 'El nombre de usuario es obligatorio',
        v => (v && v.length >= 5) || 'El nombre de usuario tiene que tener más de 5 caracteres'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail es obligatorio',
        v => /.+@.+/.test(v) || 'E-mail invalido'
      ],
      select: null,
      checkbox: false,
      password:'',
      passwordRules: [
        v => !!v || 'La contraseña es obligatoria',
        v => (v && v.length <= 8) || 'La contraseña tiene que tener menos de 8 caracteres'
      ],
    }),
    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.snackbar = true
        }
        
        
      },
      comprobar(lista){
        var i;
        if(this.name == '' || this.apellidos == '' || this.password == '' || this.email == '' || this.username == ''){
          alert('Faltan campos del formulario por rellenar.');
          return;
        }
        else{
          for( i = 0; i < lista.length; i++){
          if(lista[i].email === this.email){
            alert('El email ya está registrado en el sistema');
            return;
          }
        }
        }
        
      },
      reset () {
        this.$refs.form.reset()
      }
    },
 
  }
</script>
<style scoped>
    #registro{
        height: 100%;
        width: 80%;
        
        justify-content: center;
        align-content: center;
        margin-left: 10%;
        
    }
    
</style>