                                                    <v-text-field :dense=true background-color="white" height="50"
                                                                  label="Escribe tu respuesta" v-model="nuevaRespuesta" style="height:auto; width:500px; text-align:center; background-color:white; border:2px solid black;" />
                                                </v-card-text>

                                                <v-card-actions>
                                                    <v-spacer></v-spacer>
                                                    <v-btn color="#62C251"
                                                           text
                                                           @click="dialog2 = false">
                                                        Cancelar
                                                    </v-btn>
                                                    <div v-if="!logeado()">
                                                        <router-link style="text-decoration: none; color: inherit;" to="/registro">
                                                            <v-btn color="#62C251"
                                                                   text
                                                                   @click="responderNo">
                                                                Enviar
                                                            </v-btn>
                                                        </router-link>
                                                    </div>
                                                    <div v-else>
                                                        <div v-if="cliente()">
                                                            <v-btn color="#62C251"
                                                                   text
                                                                   @click="responder(m, 'cliente')">
                                                                Enviar
                                                            </v-btn>
                                                        </div>
                                                        <div v-else>
                                                            <v-btn color="#62C251"
                                                                   text
                                                                   @click="responder(m, 'trabajador')">
                                                                Enviar
                                                            </v-btn>
                                                        </div>
                                                    </div>
</v-card-actions>
                                            </v-card>
                                        </v-dialog>
                                    </v-row>
                                </template>
                            </div><br />
                        </div> <br />
                        <v-dialog v-model="dialog"
                                  max-width="500">
                            <v-card>
                                <v-card-title class="headline">
