                                </v-card-title>

                                <v-card-text>
                                    Si lleva a cabo esta acción, se enviará una notificación a los responsables del sitio web, para que lleven a cabo las acciones oportunas.
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>

                                    <v-btn color="#62C251"
                                           text
                                           @click="dialog = false" class="black--text">
                                        Cancelar
                                    </v-btn>

                                    <v-btn color="#62C251"
                                           text
                                           @click="dialog = false" class="black--text">
                                        Denunciar
                                    </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </div>
                </div>
            </v-col>

        </v-row>

