            <v-row>
                <v-col cols="6" align="right">

                    <router-link style="text-decoration: none; color: inherit;" to="/registro">
                        <v-btn color="#62C251" class="black--text" @click="eliminarComentarios('NoLogueado')">
                            Eliminar comentarios
                        </v-btn>
                    </router-link>
                </v-col>
                <v-col cols="6" align="left">
                    <router-link style="text-decoration: none; color: inherit;" to="/registro">
                        <v-btn color="#62C251" class="black--text" @click="denunciarComentarios('NoLogueado')">
                            Denunciar comentarios
                        </v-btn>
                    </router-link>
                </v-col>
            </v-row>
        </div>
        <div v-else>
            
            <div v-if="cliente()">
                <v-row>
                    <v-col cols="6" align="right">
                        <v-btn color="#62C251" class="black--text" @click="eliminarClient">
