                        <v-btn color="#62C251" class="black--text" @click="eliminarComentarios('trabajador')">
                            Eliminar comentarios
                        </v-btn>
                    </v-col>
                </v-row>
                
            </div>
        </div>

            
                
     
        
        <v-row>

            <v-col cols="12" align="center">
                <div class="campo" id="campo">
                    <v-text-field :dense=true background-color="white" height="50" 
                                  label="Nuevo Comentario" v-model="nuevo" align-text="center" />
                </div>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="12" align="center">
                <div v-if="!logeado()">
                    <router-link style="text-decoration: none; color: inherit;" to="/registro"> <v-btn color="#62C251" class="black--text" @click="enviar2">Enviar comentario</v-btn>  </router-link>
                </div>
                <div v-else>
                    <div v-if="cliente()">
                        <v-btn color="#62C251" class="black--text" @click="enviar('cliente')">
                            Enviar comentario
                        </v-btn>
                    </div>
                    <div v-else>
                        <v-btn color="#62C251" class="black--text" @click="enviar('trabajador')">
                            Enviar comentario
                        </v-btn>
                    </div>
                    </div>


</v-col>
