    }

    .botonResponder {
        float: right;
        border: 1px solid black;
        padding: 8px;
        margin-right: 10px;
        margin-top: 9px;
    }

    .botonDenunciar {
        float: right;
        border: 1px solid black;
        padding: 8px;
        margin-right: 10px;
        margin-top: 9px;
    }

    .botonCerrarSesion {
        background-color: #c7c9c8;
        border: 1px solid black;
        padding: 7px;
        color: black;
        text-decoration: none;
    }

    .divcomentario {
        float: left;
        margin-left: 15px;
        max-width: 560px;
    }
</style>