<template>
      <div id="general" >
          <v-row>
              <v-col cols="6" align="left">

                  <router-link style="text-decoration: none; color: inherit;" to="/perfil">
                      <v-btn color="#62C251" class="white--text">
                          Atrás
                      </v-btn>
                  </router-link>
              </v-col>
              
                  <v-col cols="1" align="center" >
                      <div style="background-color: #AEDFF0; width:55px; border-radius:45px; margin-left: 20px;">
                      <v-icon size="55" @click="ayuda" color="#1373F0"> mdi-help-circle-outline</v-icon>
                      </div>
                </v-col>
              
          </v-row>
      


      <div id = "registro">
          <v-form ref="form"
                  v-model="valid"
                  lazy-validation>


              <b>
                  <v-text-field v-model="name"
                                :rules="nameRules"
                                label="Nombre: "
                                required></v-text-field>
              </b>

              <b>
                  <v-text-field v-model="nacimiento"
                                :rules="nacimientoRules"
                                label="Fecha de nacimiento (dd-mm-yyyy): "
                                required></v-text-field>
              </b>


              <b>
                  <v-text-field v-model="sexo"
                                :rules="sexoRules"
                                :counter="7"
                                label="Sexo: "
                                required></v-text-field>
              </b>

              <b>
                  <v-text-field v-model="especie"
                                :rules="especieRules"
                                label="Especie: "
                                required></v-text-field>
              </b>

              <b>
                  <v-text-field v-model="raza"
                                :rules="razaRules"
                                label="Raza: "
                                required></v-text-field>
              </b>

              <b>
                  <v-text-field v-model="peso"
                                :rules="pesoRules"
                                label="Peso: "
                                required></v-text-field>
              </b>

              <v-checkbox v-model="checkbox"
                          :rules="[v => !!v || 'Debes aceptar los términos de privacidad para continuar']"
                          label="Por favor, acepta los términos de privacidad"
                          required></v-checkbox>
              <v-row>
                  <v-col cols="5" align="center">
                      <v-btn v-if=" !checkbox"
                             :disabled="!valid"
                             color="#208A0E"
                             @click="validate">
                          Validar
                      </v-btn>

                      <router-link v-if=" checkbox" to="/perfil">
                          <v-btn :disabled="!valid"
                                 color="#208A0E"
                                 @click="validate">
                              Validar
                          </v-btn>
                      </router-link>
                  </v-col>

                  <v-col cols="5" align="center">
                      <v-btn color="#B81720"
                             @click="reset">
                          Restaurar el formulario
                      </v-btn>
                  </v-col>
              </v-row>

          </v-form>
      </div>
    </div>
</template>
<script>
  
  export default {
    data: () => ({
      
      valid: false,
      name: '',
      usuarios: [],
      nameRules: [
        v => !!v || 'El nombre es obligatorio',
        
      ],
      nacimiento:'',
      nacimientoRules:[
        
        v => !!v || 'Añade la fecha de nacimiento',
      ],
      sexo:'',
      sexoRules:[
        
        v => !!v || 'Añade el sexo de la mascota',
        v => (v && v.length <= 7) || 'El sexo de la mascota tiene que tener menos de 7 caracteres'
      ],
      especie: '',
      especieRules: [
        v => !!v || 'Especie es obligatorio',
      ],
      raza: '',
      razaRules: [
        v => !!v || 'Raza es obligatorio',
      ],
      peso: '',
      pesoRules: [
        v => !!v || 'Peso es obligatorio',
      ],
      select: null,
      checkbox: false,
      
    }),
    methods: {
          validate () {
                if (this.$refs.form.validate()) {
                  this.snackbar = true
                }
        
        
          },
          ayuda() {
              alert('Rellena todos los campos con los datos de tu mascota, acepta los términos de privacidad y pulsar en "Validar". Pulsa en restaurar formulario si desea comenzarlo desde el principio.')
          },
          comprobar(lista){
                var i;
                if(this.name == '' || this.apellidos == '' || this.password == '' || this.email == ''){
                    alert('Faltan campos del formulario por rellenar.');
                    return;
                }
                else{
                  for( i = 0; i < lista.length; i++){
                      if(lista[i].email === this.email){
                          alert('El email ya está registrado en el sistema');
                          return;
                      }
                    }
                }
        
          },
          reset () {
            this.$refs.form.reset()
          }
    },
 
  }
</script>
<style scoped>
    #registro{
        height: 100%;
        width: 80%;
        
        justify-content: center;
        align-content: center;
        margin-left: 10%;
        
    }
    
</style>