<template>
  <div>
    <v-layout >
        
      <v-card color="#e3cf98"
        width="100%">
        <v-container>
          <v-row >
            <v-col cols="2" align="center">
              <v-icon x-large> mdi-paw </v-icon>
            </v-col>
            <v-col cols="7" >
              <v-text-field 
                :dense=true
                label = "Usuario"
                v-model="usuario"
                prepend-icon="mdi-account-circle"
              />
            </v-col>
            <v-col cols="2" align="center">
              <v-icon x-large> mdi-paw </v-icon>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="2" align="center">
              <v-icon x-large> mdi-paw </v-icon>
            </v-col>
            <v-col cols="7" >
            <v-text-field
              :dense=true
              :type="showPassword ? 'text' : 'password'"
              label = "Contraseña"
              v-model="contrasenya"
              prepend-icon="mdi-lock"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              
              @click:append="showPassword = !showPassword"
            />
            </v-col>
            <v-col cols="2"  align="center">
              <v-icon x-large> mdi-paw </v-icon>
            </v-col>
          </v-row>        

          <v-row>
            <v-col cols="1" >
            </v-col>

            <v-col cols="2" align="center">
              <router-link style="text-decoration: none; color: inherit;" to="/registro" replace>
                <v-btn color="#62C251"  class="black--text" >
                  Registrar
                </v-btn> 
              </router-link>
            </v-col>
            <v-col cols="3" >
            </v-col>

            <v-col cols="2">
                <div v-if="logear()">
                    <div v-if="cliente">
                        <router-link style="text-decoration: none; color: inherit;" to="/cliente">
                         <v-btn color="#62C251" class="black--text">Iniciar Sesión</v-btn>  </router-link>
                    </div>
                    <div v-else>
                        <router-link style="text-decoration: none; color: inherit;" to="/trabajador"> 
                        <v-btn color="#62C251" class="black--text">Iniciar Sesión</v-btn>  </router-link>
                    </div>
                </div>
                <div v-else>
                    <v-btn color="#62C251" class="black--text" @click="error"> Iniciar Sesión </v-btn>
                </div>
                
            </v-col>

            <v-col cols="1" >
            </v-col>
          </v-row>

        </v-container>
      </v-card>
    </v-layout> 
    

    </div>
</template>

<script>

    export default {
        data: () => ({
            showPassword: false,
            usuario: '',
            contrasenya: '',
            cliente: false,
        }),
        methods: {
            logear() {
                if (this.usuario == "cliente" && this.contrasenya == "accesibilidad") {
                    this.cliente = true
                    return true
                }
                else if (this.usuario == "trabajador" && this.contrasenya == "accesibilidad") {
                    this.cliente = false
                    return true
                }
                else {
                    this.cliente = false
                    return false
                }
            },
            error() {
                alert('Usuario o contraseña incorrecta.')
            }
            
        }, 
        

    }

</script>

<style>   
  
</style>