<template>
  <div> 
    <v-row>
        <v-col cols="2">
        </v-col>
        <v-col cols="8" align="center"> 
            
            <v-data-table
                :headers="headers"
                :items="servicios"
                :search="search"
                :options.sync="pagination"
                class="elevation-1"
            >
                <template v-slot:items="props">
                <td>{{ props.item.name }}</td>
                <td class="text-xs-right">{{ props.item.precios }}</td>
                </template>
            </v-data-table>
                    
        </v-col>

        <v-col cols="2">
        </v-col>

    </v-row>

  </div>
    
</template>

<script>
  export default {
    data () {
      return {
        search: '',
        pagination: {},
        selected: [],
        headers: [
          {
            text: 'Servicio',
            align: 'left',
            sortable: false,
            value: 'name'
          },
          { text: 'Precio con I.V.A.',
            align: 'center',
            value: 'precio' }
        ],
        servicios: [
            {
            name: 'Consulta',
            precio: 'GRATIS'
            },
            {
            name: 'Vacuna de la rabia',
            precio: 19
            },
            {
            name: 'Vacuna tetravalente canina',
            precio: 32
            },
            {
            name: 'Vacuna trivalente canina',
            precio: 21
            },
            {
            name: 'Implantación del microchip',
            precio: 32
            },
            {
            name: 'Consulta urgencia vital',
            precio: 48
            },
            {
            name: 'Consulta de especialidad',
            precio: 55
            },
            {
            name: 'Certificado veterinario oficial (incluye consulta)',
            precio: 33
            },
            {
            name: 'Pasaporte intracomunitario',
            precio: 13
            },
            {
            name: 'Cambio Propietario',
            precio: 18
            },
            {
            name: 'Castración canina macho',
            precio: 108
            },
            {
            name: 'Castración felina macho',
            precio: 94
            },
            {
            name: 'Castración hembra',
            precio: 148
            },
            {
            name: 'Anestesia',
            precio: 22
            },
            {
            name: 'Ecografía',
            precio: 41
            },
        ]
      
    }
  },
     methods: {
      toggleOrder () {
        this.pagination.descending = !this.pagination.descending
      },
      nextSort () {
        let index = this.headers.findIndex(h => h.value === this.pagination.sortBy)
        index = (index + 1) % this.headers.length
        index = index === 0 ? index + 1 : index
        this.pagination.sortBy = this.headers[index].value
      }
    }
  }
</script>

