import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import RegistrarVista from '../views/RegistrarVista.vue'
import ForoVista from '../views/ForoVista.vue'
import ClienteVista from '../views/ClienteVista.vue'
import TrabajadorVista from '../views/TrabajadorVista.vue'
import PerfilVista from '../views/PerfilVista.vue'
import CitasVista from '../views/CitasVista.vue'
import EliminarMascotaVista from '../views/EliminarMascotaVista.vue'
import EditarMascotaVista from '../views/EditarMascotaVista.vue'
import TarifasVista from '../views/TarifasVista.vue'
import AnyadirCitaVista from '../views/AnyadirCitaVista.vue'
import AnyadirFotoVista from '../views/AnyadirFotoVista.vue'
import AnyadirMascotaVista from '../views/AnyadirMascotaVista.vue'
import ConfirmarCitaVista from '../views/ConfirmarCitaVista.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/registro',
    name: 'RegistrarVista',
    component: RegistrarVista
  },
  {
    path: '/anyadirMascota',
    name: 'AnyadirMascotaVista',
    component: AnyadirMascotaVista
  },
  {
    path: '/foro',
    name: 'ForoVista',
    component: ForoVista,
    props: true
  },
  
  {
    path: '/cliente',
    name: 'ClienteVista',
    component: ClienteVista
  },
  {
    path: '/trabajador',
    name: 'TrabajadorVista',
    component: TrabajadorVista
  },
  {
    path: '/perfil',
    name: 'PerfilVista',
    component: PerfilVista
  },
  {
    path: '/citas',
    name: 'CitasVista',
    component: CitasVista,
    props: true
    },
  {
      path: '/eliminarMascota',
      name: 'EliminarMascotaVista',
      component: EliminarMascotaVista
  },
  {
    path: '/editarMascota',
    name: 'EditarMascotaVista',
    component: EditarMascotaVista
  },
  {
    path: '/tarifas',
    name: 'TarifasVista',
    component: TarifasVista,
    props: true
  },
  {
    path: '/anyadirCita',
    name: 'AnyadirCita',
    component: AnyadirCitaVista
  },
  {
    path: '/anyadirFoto',
    name: 'AnyadirFoto',
    component: AnyadirFotoVista
  },
  {
    path: '/eliminarMascota',
    name: 'EliminarMascota',
    component: EliminarMascotaVista
  },
  {
    path: '/confirmarCita',
    name: 'ConfirmarCita',
    component: ConfirmarCitaVista
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
