<template>

  <v-row>
    <v-col cols="2">
      <v-spacer> </v-spacer>
    </v-col>
    <v-col cols = "8">
      <v-data-table
        :headers="headers"
        :items="mascotas"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-toolbar-title>Mis Mascotas</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-dialog
              v-model="dialog"
              max-width="500px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="#62C251"
                  dark
                  class="black--text"
                  v-bind="attrs"
                  v-on="on"
                >
                  Nueva Mascota
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="6"
                        md="4"
                      >
                        <v-text-field
                          v-model="editedItem.nombreMascota"
                          label="Nombre Mascota"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="6"
                        md="4"
                      >

                        <v-menu
                           label="Fecha de nacimiento"
                          ref="menu3"
                          v-model="menu3"
                          :close-on-content-click="false"
                          :nudge-right="40"
                          :return-value.sync="time"
                          transition="scale-transition"
                          offset-y
                          max-width="290px"
                          min-width="290px"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="editedItem.fechaNacimiento"
                              label="Fecha"
                              v-bind="attrs"
                              v-on="on"
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="menu3"
                            name="calendario"
                            v-model="editedItem.fechaNacimiento"
                            full-width
                            @click:minute="$refs.menu3.save(date)"
                          ></v-date-picker>
                        </v-menu>



                      </v-col>
                      <v-col
                        cols="12"
                        sm="6"
                        md="4"
                      >
                        <v-select
                          v-model="editedItem.sexo"
                          :items="generos"
                          item-text="genero"
                          label="Sexo"
                          single-line
                        ></v-select>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="6"
                        md="4"
                      >
                        <v-select
                          v-model="editedItem.especie"
                          :items="items"
                          item-text="especies"
                          label="Especie"
                          single-line
                          
                        ></v-select>
                        
                      </v-col>
                      <v-card v-if="manual">
                          
                          <v-card-title >Introduzca la especie que desea:</v-card-title>
                          <v-card-actions>
                            
                            <v-text-field
                              v-model="editedItem.especie"
                              label="Especie"
                            ></v-text-field>
                          </v-card-actions>
                        </v-card>
                      <v-col
                        cols="12"
                        sm="6"
                        md="4"
                      >
                        <v-text-field
                          v-model="editedItem.raza"
                          label="Raza"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="6"
                        md="4"
                      >
                        <v-text-field
                          v-model="editedItem.peso"
                          label="Peso(kg)"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="save"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline">Estas seguro de que quieres eliminar a esta mascota?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete">No</v-btn>
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm">Si</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.acciones="{ item }">
          <v-icon
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
        </template>
        <template v-slot:no-data>
          <v-btn
            color="primary"
            @click="initialize"
          >
            Reset
          </v-btn>
        </template>
      </v-data-table>
    </v-col>
  </v-row>


</template>


<script>
  export default {
    data: () => ({
      time: null,
      menu2: false,
      menu3: false,
      manual: false,
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Nombre',
          align: 'start',
          sortable: false,
          value: 'nombreMascota',
        },
        { text: 'Fecha de Nacimiento', value: 'fechaNacimiento',  sortable: false },
        { text: 'Sexo', value: 'sexo', sortable: false },
        { text: 'Especie', value: 'especie', sortable: false },
        { text: 'Raza', value: 'raza', sortable: false },
        { text: 'Peso (kg)', value: 'peso' },
        { text: 'Acciones', value: 'acciones', sortable: false },
      ],
      mascotas: [],
      editedIndex: -1,
      editedItem: {
        nombreMascota: '',
        fechaNacimiento: '',
        sexo: '',
        especie: '',
        raza: '',
      },
      defaultItem: {
        nombreMascota: '',
        fechaNacimiento: '',
        sexo:'',
        especie: '',
        raza: '',
      },
      generos: [
        {genero: 'Macho'},
        {genero: 'Hembra'},
      ],
      items: [
        {especies: 'Canino'},
        {especies: 'Felino'},
        {especies: 'Pájaro'},
        {especies: 'Pez'},
        {especies: 'Reptil'},
        {especies: 'Otro'},
      ],
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Nueva mascota' : 'Editar mascota'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },
    created () {
      this.initialize()
    },
    methods: {
      initialize () {
        this.mascotas = [
          {
            nombreMascota: 'Kira',
            fechaNacimiento: '2008-12-01',
            sexo: 'Hembra',
            especie: 'Canino',
            raza: 'Cocker',
            peso: 14,
            
          },
          {
            nombreMascota: 'Enzo',
            fechaNacimiento: '2020-06-26',
            sexo: 'Macho',
            especie: 'Canino',
            raza: 'Mini Pincher',
            peso: 3,
          },
          {
           nombreMascota: 'Tina',
            fechaNacimiento: '2008-09-15',
            sexo: 'Hembra',
            especie: 'Canino',
            raza: 'Teckel',
            peso: 13,
          },
        ]
      },
        editItem(item) {
                this.editedIndex = this.mascotas.indexOf(item)
                this.editedItem = Object.assign({}, item)
                this.dialog = true
      },
      deleteItem (item) {
        this.editedIndex = this.mascotas.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },
      deleteItemConfirm () {
        this.mascotas.splice(this.editedIndex, 1)
        this.closeDelete()
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      save () {
        if (this.editedIndex > -1) {
            if (this.editedItem.nombreMascota == '' || this.editedItem.fechaNacimiento == '' || this.editedItem.sexo == ''
                || this.editedItem.especie == '' || this.editedItem.raza == '' || this.editedItem.peso == '')
                alert('Debes rellenar todos los campos para modificar los datos de tu mascota.')
            else {
                var hoy = new Date();
                if (Date.parse(this.editedItem.fechaNacimiento) > hoy)
                    alert('¡Escoge una fecha de nacimiento anterior a hoy!')
                else {
                    if (this.editedItem.especie == "Otro")
                        this.cuadro()
                    else {

                        Object.assign(this.mascotas[this.editedIndex], this.editedItem)

                        this.editedItem.nombreMascota == ''
                        this.editedItem.fechaNacimiento == ''
                        this.editedItem.sexo == ''
                        this.editedItem.especie == ''
                        this.editedItem.raza == ''
                        this.editedItem.peso == ''
                        this.manual = false
                        this.close()
                    }
                }
            }
            

        } else {
          if (this.editedItem.nombreMascota == '' || this.editedItem.fechaNacimiento == '' || this.editedItem.sexo == ''
              || this.editedItem.especie ==''|| this.editedItem.raza =='' || this.editedItem.peso =='') 
              alert('Debes rellenar todos los campos para añadir la mascota.')
              else{
                var hoy = new Date();
                if(Date.parse(this.editedItem.fechaNacimiento) > hoy) 
                  alert('¡Escoge una fecha de nacimiento anterior a hoy!')
                else{
                    if(this.editedItem.especie=="Otro")
                    this.cuadro()                      
                    else{

                      this.mascotas.push(this.editedItem)
                      
                      this.editedItem.nombreMascota == ''
                      this.editedItem.fechaNacimiento == ''
                      this.editedItem.sexo == ''
                      this.editedItem.especie == ''
                      this.editedItem.raza == ''
                      this.editedItem.peso == ''
                      this.manual = false
                      this.close()
                }
              }
              }
        }
      },
      cuadro (){
          this.manual=true
      },
    },
  }
</script>