<template>

        <v-row>
            <v-col cols="2">
                <v-spacer> </v-spacer>
            </v-col>
            <v-col cols="8" >
                <v-data-table
                    :headers="headers"
                    :items="mascotas"
                    sort-by="date"
                    class="elevation-1"
                >
                    <template v-slot:top>
                        <v-toolbar
                            flat
                        >
                            <v-toolbar-title>Confirmar Cita</v-toolbar-title>        
                            <v-divider
                            class="mx-4"
                            inset
                            vertical
                            ></v-divider>
                            <v-spacer></v-spacer>
                            <v-dialog
                            v-model="dialog"
                            max-width="500px"
                            >
                            
                            <v-card>
                                <v-card-title>
                                <span class="headline">{{ formTitle }}</span>
                                </v-card-title>

                                <v-card-text>
                                <v-container>
                                    <v-row>
                                    <v-col
                                        cols="12"
                                        sm="6"
                                        md="4"
                                    >
                                        <v-text-field
                                        v-model="editedItem.name"
                                        label="Nombre Mascota"
                                        ></v-text-field>
                                    </v-col>
                                    <v-col
                                        cols="12"
                                        sm="6"
                                        md="4"
                                    >
                                        <v-text-field
                                        v-model="editedItem.date"
                                        label="Fecha"
                                        ></v-text-field>
                                    </v-col>
                                    <v-col
                                        cols="12"
                                        sm="6"
                                        md="4"
                                    >
                                        <v-text-field
                                        v-model="editedItem.time"
                                        label="Hora"
                                        ></v-text-field>
                                    </v-col>
                                    </v-row>
                                </v-container>
                                </v-card-text>

                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="close"
                                >
                                    Cancelar
                                </v-btn>
                                <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="save"
                                >
                                    Guardar
                                </v-btn>
                                </v-card-actions>
                            </v-card>
                            </v-dialog>
                            <v-dialog v-model="dialogDelete" max-width="500px">
                            <v-card>
                                <v-card-title class="headline">¿ Está seguro de que desea borrar la cita seleccionada ?</v-card-title>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                                <v-btn color="blue darken-1" text @click="deleteItemConfirm">Si</v-btn>
                                <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>
                            </v-dialog>

                            <v-dialog v-model="dialogConfirm" max-width="500px">
                            <v-card>
                                <v-card-title class="headline">¿ Está seguro de que desea Confirmar la cita seleccionada ?</v-card-title>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="closeConfirm">Cancelar</v-btn>
                                <v-btn color="blue darken-1" text @click="confirmItemConfirm">Si</v-btn>
                                <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>
                            </v-dialog>
                        </v-toolbar>
                    </template>
                    <template v-slot:item.actions="{ item }">
                    <v-icon
                        small
                        class="mr-2"
                        @click="confirmItem(item)"

                    >
                        mdi-check
                    </v-icon>
                    <v-icon
                        small
                        @click="deleteItem(item)"
                    >
                        mdi-close-circle
                    </v-icon>
                    </template>
                    <template v-slot:no-data>
                    <v-btn
                        color="primary"
                        @click="initialize"
                    >
                        Restaurar
                    </v-btn>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
    
</template>
<script>
  export default {
    data: () => ({
      dialog: false,
      dialogDelete: false,
      dialogConfirm: false,
      headers: [
        {
          text: 'Nombre Mascota',
          align: 'start',
          sortable: false,
          value: 'name',
        },
        { text: 'Date', value: 'date' },
        { text: 'Hora', value: 'time' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      mascotas: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        date: '',
        time: ''
      },
      defaultItem: {
        name: '',
        date: '',
        time: ''
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
      dialogConfirm (val) {
        val || this.closeConfirm()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.mascotas = [
         {
            name: 'Kira',
            date: '15/12/2020',
            time: '16:45'
        },
        {
            name: 'Zeus',
            date: '15/12/2020',
            time: '17:45'
        },
        {
            name: 'Flufly',
            date: '16/12/2020',
            time: '18:00'
        },
        {
            name: 'Kira',
            date: '15/12/2020',
            time: '16:45'
        },
        {
            name: 'Zeus',
            date: '15/12/2020',
            time: '17:45'
        },
        {
            name: 'Flufly',
            date: '16/12/2020',
            time: '18:00'
        }
        ]
      },

      editItem (item) {
        this.editedIndex = this.mascotas.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.mascotas.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      confirmItem (item) {
        this.editedIndex = this.mascotas.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogConfirm = true  
      },

      deleteItemConfirm () {
        this.mascotas.splice(this.editedIndex, 1)
        this.closeDelete()
      },
      confirmItemConfirm (){
        this.mascotas.splice(this.editedIndex, 1)
        this.closeConfirm()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      closeConfirm (){
        this.dialogConfirm = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.mascotas[this.editedIndex], this.editedItem)
        } else {
          this.mascotas.push(this.editedItem)
        }
        this.close()
      },
    },
  }

</script>

 